.video-bg {
    position: absolute;
    height: 100vh !important;
    object-fit: cover !important;
    overflow: hidden;
    z-index: -100;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    filter: blur(.15rem);
}

body {
    background: url(../../assets/img/bg.jpg) no-repeat center center;
    background-size: cover;
    min-height: 100vh !important;

}